.skills-content .sub-container {
  margin: 0 auto 3rem auto;
  width: 70%;
}

.skills-header {
  text-align: center;
  font-size: 2.5rem;
}

.skill-icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  margin: auto;
}

.skill {
  width: 100px;
  height: 100px;
  margin: 1.8rem;
  transition: 200ms ease-in-out;
}

.skill:hover {
  transform: scale(1.1);
  transition: 200ms ease-in-out;
}

.skill-logo {
  font-size: 6rem;
  margin: auto;
}

.skill-name {
  text-align: center;
  font-weight: 700;
}

#angular + p,
#angular {
  color: #c3002f;
}

#alpine + p,
#alpine {
  color: #8bc0d0;
}

#aws + p,
#aws {
  color: #ff9900;
}

#csharp + p,
#csharp {
  color: #512bd4;
}

#css3 + p,
#css3 {
  color: #1572b6;
}

#html5 + p,
#html5 {
  color: #e34f26;
}

#ionic + p,
#ionic {
  color: #3880ff;
}

#javascript + p,
#javascript {
  color: #f7df1e;
}

#nestjs + p,
#nestjs {
  color: #e0234e;
}

#nextjs + p,
#nextjs {
  color: var(--light);
}

#nodejs + p,
#nodejs {
  color: #339933;
}

#php + p,
#php {
  color: #777bb4;
}

#react + p,
#react {
  color: #61dafb;
}

#sqlite + p,
#sqlite {
  color: #003b57;
}

#tailwind + p,
#tailwind {
  color: #06b6d4;
}

#typescript + p,
#typescript {
  color: #3178c6;
}

#vite + p,
#vite {
  color: #646cff;
}

#docker + p,
#docker {
  color: #0db7ed;
}

#mongodb + p,
#mongodb {
  color: #13aa52;
}

#expo + p,
#expo {
  color: var(--light);
}

@media only screen and (max-width: 1200px) {
  .skills-content {
    width: 100%;
  }
  .skill-icon-container {
    margin-top: 1rem;
    width: 100%;
  }
}
@media only screen and (max-width: 768px) {
  .skills-content .sub-container {
    width: 100%;
  }
  .skill {
    text-align: center;
    width: 80px;
    height: 80px;
    margin: 1rem 0.5rem;
  }
  .skill-logo {
    margin: auto;
    font-size: 3rem;
  }
}
